import { useContext } from "react";
import { AuthContext } from "../context";
import loginpage from "./login.module.css";
export default function Login() {
  let { AuthUser, Authtogel } = useContext(AuthContext);

  console.log(AuthUser, "s");

  return (
    <div className={loginpage.maindata}>
      <div className={loginpage.box}>
        <h1>Login page</h1>

        <input type="text" placeholder="Enter Name" />
        <input type="password" placeholder="Enter Password" />
        <button onClick={Authtogel}>Login</button>
      </div>
      <h1>User Login:- {AuthUser ? "yes" : "no"}</h1>
    </div>
  );
}
