import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";

const getData = (url) => {
  return fetch(url).then((res) => res.json());
};

export default function SingleProduct() {
  let [data, setdata] = useState({});

  let { id } = useParams();

  useEffect(() => {
    getData(`https://fakestoreapi.com/products/${id}`).then((res) => {
      setdata(res);
    });
  }, [id]);

  console.log(data);

  return (
    <div className="App" style={{ width: "350px", margin: "auto" }}>
      <h1>user is :- {id}</h1>
      <img width="100%" src={data.image} alt="product" />
      <p> catogory :-{data.title}</p>
      <p>description :-{data.description}</p>
      <p>price is {data.price}</p>
      <Link to="/">
        <button>home</button>
      </Link>
      <br />
      <br />
      <Link to="/product">
        <button>product</button>
      </Link>
    </div>
  );
}
