import { useContext, useEffect, useState } from "react";
import { Link, Navigate, NavLink, useSearchParams } from "react-router-dom";
import { AuthContext } from "../context";
import productgrid from "./product.module.css";
export default function Product() {
  let [product, setproduct] = useState([]);

  let [repage, setrepage] = useSearchParams();
  let innitialpage = convertnumber(repage.get("page"));
  let { AuthUser, Authtogel } = useContext(AuthContext);

  let [page, setpage] = useState(innitialpage);

  let [text, settext] = useState("");

  useEffect(() => {
    setrepage({ page, text });
  }, [page, text]);

  function convertnumber(value) {
    value = Number(value);
    if (typeof value === "number" && value <= 0) {
      value = 1;
    }
    if (!value) {
      value = 1;
    }
    return value;
  }

  if (!AuthUser) {
    return <Navigate to="/" />;
  }

  let getdata = async (page) => {
    fetch(`https://reqres.in/api/users?page=${page}`)
      .then((res) => res.json())
      .then((json) => setproduct(json.data));
  };

  useEffect(() => {
    getdata(page);
  }, [page]);

  let singleproduct = (id) => {
    return id;
  };

  return (
    <div className="home">
      <h1>text bar </h1>
      <input
        value={text}
        onChange={(e) => settext(e.target.value)}
        type="text"
      />
      <h1>Product</h1>

      <div className={productgrid.grid}>
        {product.map((ele) => {
          return (
            <div className={productgrid.box}>
              <img src={ele.avatar} alt="product" style={{ width: "100px" }} />
              <p>Product Categoary:-{ele.email}</p>
              <p>Name :-{ele.first_name}</p>
              <button onClick={() => singleproduct(ele.id)}>
                {" "}
                <NavLink to={`/product/${ele.id}`}>More</NavLink>
              </button>
            </div>
          );
        })}
      </div>
      <br />

      <button disabled={page === 1} onClick={() => setpage(page - 1)}>
        Prew
      </button>
      <button>{page}</button>
      <button disabled={page === 2} onClick={() => setpage(page + 1)}>
        Next
      </button>
    </div>
  );
}
